import Vue from 'vue'
import '../../assets/js/bootstrap'
import { api } from '../../assets/js/url'
import { Ajax, Common } from '../../assets/js/common'
import Page from '../page.vue'

Vue.component('page', Page)

var articles = {
  label: {
    selected: '全部',
    allLabel: []
  },
  type: {
    selected: '全部',
    allType: ['全部', '原创', '翻译', '转载']
  },
  article: [],
  page: 1,
  pages: 1
}

export default {
  name: 'article',
  beforeMount () {
    Ajax.getLabel(this.getLabel)
    this.getPage(this.page)
  },
  methods: {
    chooseType (item) {
      this.type.selected = item
    },
    getLabel (data) {
      this.label.allLabel = data.labels
    },
    removeArticle (id) {
      var removeDate = {
        'id': id,
        'page': this.page
      }
      Ajax.post(api.content.remove, removeDate, (data) => {
        var _this = this
        console.log('data.content:' + data.content.length)
        this.article = data.content.map(function (item) {
          var time = new Date(item.addTime)
          item.addTime = _this.getTime(time)
          return item
        })
        this.page = data.page
        this.pages = data.pages
      })
    },
    getPage (page) {
      Ajax.getArticle(page, (data) => {
        this.article = data.content.map(function (item) {
          var time = new Date(item.addTime)
          item.addTime = Common.getTime(time)
          return item
        })
        this.page = data.page
        this.pages = data.pages
      })
    }
  },
  data () {
    return articles
  }
}



