import Vue from 'vue'
import $ from 'jquery'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import Router from '../../router/index'
import { StorageUtil } from '../../assets/js/Auth'

Vue.config.productionTip = false

/* eslint-disable no-new */
// use
Vue.use(mavonEditor)

export default {
  name: 'write',
  beforeMount () {
    if (!StorageUtil.getObject('userInfo')) {
      Router.push('/')
    }
  },
  mounted () {
    this.getLabel()
  },
  methods: {
    saveArticle () {
      $.post('/admin/article/save', this.article, (data) => {
        console.log(data)
      })
    },
    addLabel () {
      this.send = !this.send
    },
    getLabel () {
      $.post('/admin/label/search', (data) => {
        this.commandlabel = data.labels
      })
    }
  },
  data () {
    return {
      login: StorageUtil.getObject('userInfo'),
      article: {
        value: '', // 文章内容
        title: '', // 文章标题
        abstract: '', // 摘录
        label: '5a06abaeafcd16209447cd5a', // 添加标签
        user: StorageUtil.getObject('userInfo').id
      },
      commandlabel: [], // 推荐标签
      send: false,
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: false, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true // 预览
      }
    }
  }
}



